<template>
	<div class="container">
		<header class="header">
			<div class="logo" @click="goToMainPage">
				MovieApp
			</div>
			<div class="films-items">
				<div @click="getTop100" class="films-items__block"> Топ 100 </div>
				<div @click="getAwaitMovies" class="films-items__block"> Самые ожидаемые </div>
			</div>
			<div class="search">
				<input class="search-input" type="text" placeholder="Поиск..." v-model="search">
				<button class="search-button" type="button" @click="serchMovie"> Найти </button>
			</div>
		</header>
	</div>	
</template>

<script>

   import { mapActions } from 'vuex';
	
   export default {
   		name: 'VHeader',
   		data() {
   			return {
   				search: '',
   			}
   		},
   		methods: {
   			...mapActions(['changeFilms', 'loadTop100', 'loadAwaitMovies', 'setBaseUrl', 'load']),
   			serchMovie() {
   				if (this.search !== '') {
   					this.changeFilms(this.search);
   					this.search = '';
   				}
   			},
   			getTop100() {
   				this.loadTop100();
   			},
   			getAwaitMovies() {
   				this.loadAwaitMovies();
   			},
   			goToMainPage() {
   				this.setBaseUrl();
   				this.load();
   			}
   		}
   };

</script>

<style lang="scss">

	.header {
		color: $white;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 60px;
	}

	.logo {
		font-size: 32px;
		font-weight: 300;
		cursor: pointer;
	}

	.films-items {
		display: flex;
		align-items: center;

		&__block {
			margin: 0 20px;
			cursor: pointer;
			font-size: 24px;
			font-weight: 300;
		}
	}

	.search-input {
		max-width: 175px;
		border: none;
		border-radius: 5px;
		padding: 5px 12px;

		&:focus {
			outline: none;
		}
	}

	.search-button {
		padding: 5px 12px;
		border: none;
		border-radius: 5px;
		background: $white;
		cursor: pointer;
		margin-left: -7px;
	}

	.films-items {
		color:  #fff;
	}

	.logo {
		color: #fff;
	}
</style>