<template>
	<div class="pagination-block">
		<div class="wrapper-button wrapper-button--prev"
			 @click="onPrevButton">
			Prev
		</div>
		<div class="pagination-wrapper" ref="wrapper">
			<ul class="pagination" ref="pagination">
				<li class="pagination__item"  v-for="number in paginationCount" :key="number">
					<button class="pagination__button"
							@click="nextPage(number)" :class="{'active': number === currentPage}">
						{{ number }}
					</button>
				</li>
			</ul>
		</div>
		<div class="wrapper-button wrapper-button--next"
			 @click="onNextButton">
			Next
		</div>
	</div>
</template>

<script>
	
	import { mapGetters, mapActions } from 'vuex';

	export default {
		name: 'VPagination',
		data() {
			return {
				
			}
		},
		computed: {
			...mapGetters(['pagesCount', 'currentPage']),
			paginationCount() {
				let block = [];
				for (let i = 0; i <= this.pagesCount; i++) {
					block.push(i);
				}
				let a = block.splice(this.currentPage, 3);
				return a;
			}
		},
		methods: {
			...mapActions(['setPage', 'load']),
			nextPage(number) {
				this.setPage(number);
				this.load(number);
			},
			onNextButton() {
					if ( this.currentPage < this.pagesCount ) {
						this.setPage(this.currentPage + 1);
						this.load(this.currentPage);
					}
			},
			onPrevButton() {
				if (this.currentPage > 1) {
					this.setPage(this.currentPage - 1);
					this.load(this.currentPage);
				}
			}
		}
	};

</script>

<style lang="scss">
	
	.a {
		color:  red;
	}

	.pagination-wrapper {
		display: flex;
	}

	.pagination {
		display: flex;

		&__item {
			min-width: 58px;
			min-height: 36px;
		}

		&__button {
			width: 100%;
			height: 100%;
			cursor: pointer;
			border: 2px solid $main-color;
			background: #262424;
			color: $white;

		}
	}

	.active {
		border: 2px solid yellow;
	}

	.pagination-block {
		display: flex;
	}

	.wrapper-button {
			width: 58px;
			height: 36px;
			cursor: pointer;
			border: 2px solid $main-color;
			background: #262424;
			color: $white;
			display: flex;
			justify-content: center;
			align-items: center;
		}

</style>