<template>
	
	<div class="movie-item">
		<div class="movie-rating" :class="ratingClass">
				{{ film.rating }}
		</div>
		<img :src="film.posterUrl" :alt="film.nameRu" class="movie-image">
		<div class="movie-mask">
			<router-link :to="`/movie/${film.filmId}`" @click.native="scrollToTop" class="movie-link">
				Перейти на страницу фильма
			</router-link>
		</div>
	</div>

</template>

<script>


	export default {
		name: 'MovieItem',
		props: {
			film: {
				type: Object,
				required: true
			}
		},
		computed: {
			ratingClass() {
				return {
					'green': this.film.rating >= 7,
					'red': this.film.rating <= 5,
					'orange': this.film.rating > 5,
				}
			}
		},
		methods: {
			scrollToTop() {
                window.scrollTo(0,0);
           }
		}
	};

</script>

<style lang="scss" scoped>
	
	.movie-item {
		width: 240px;
		height: 360px;
		margin: 30px 20px;
		position: relative;
	}

	.movie-image {
		object-fit: cover;
		width: 100%;
		height: 100%;
	}

	.movie-rating {
		position: absolute;
		top: 10px;
		left: 10px;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		background: green;
		display: flex;
		align-items: center;
		justify-content: center;
		color: $white;
		background: $main-color;
		font-size: 14px;
	}

	.movie-mask {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		transition: 0.5s;
		opacity: 0;
		pointer-events: none;
		visibility: hidden;
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba(0, 0, 0, 0.5);
		font-size: 18px;
	}

	.movie-item:hover .movie-mask {
		opacity: 1;
		pointer-events: all;
		visibility: visible;
	}

	.movie-link {
		color: yellow;
	}	

	.green {
		border: 1px solid green;
	}

	.orange {
		border: 1px solid orange;
	}

	.red {
		border: 1px solid red;
	}

</style>