<template>
  <div class="movie-card">
  	<div class="container">
  		<div class="card-main">
			<div class="card-main__img-container">
				<img :src="movie.posterUrl" alt="movie.nameRu" class="card-main__img">
			</div>
			<div class="card-main__information">
				<h2 class="card-main__title"> {{  movie.slogan }} </h2>
				<p> {{ movie.description }} </p>
				<div class="card-main__about">
					<p class="card-main__item"> <span class="item-name"> Премьера: </span>  {{ premierYear === 0 ? movie.year : premierYear }} </p>
					<p class="card-main__item" v-if="movie.premiereWorldCountry"> <span class="item-name"> Страна: </span>  {{ movie.premiereWorldCountry }} </p>
					<p class="card-main__item"> <span class="item-name"> Жанр: </span>  <span v-for="(item, i) in movie.genres" :key="i" class="genre-span">
						{{ item.genre }}
					 </span>
					</p>
					<p class="card-main__item" v-if="movie.ratingAgeLimits"> <span class="item-name"> Возрастное ограничение: </span> {{ movie.ratingAgeLimits }}+ </p>
					<p class="card-main__item" v-if="movieDuration !== 0"> <span class="item-name"> Продолжительность: </span>  {{ movieDuration }} минут </p>
				</div>
				<a :href="movie.webUrl" class="button-link" target="_blank">
					Найти на кинопоиске
				</a>
			</div>
		</div>
		<div class="movie-facts" v-if="length > 0">
			<p class="movie-facts__title">
				Интересные факты о фильме
			</p>
			<ul class="movie-facts__list">
				<li class="movie-facts__item"
				    v-for='(fact, i) in movie.facts'
				    :key="i" v-html="fact">
				</li>
			</ul>
		</div>

		<router-link :to="{ name: 'Home'}" class="home-link"> 
			Вернуться на главную
		</router-link>
  	</div>
   </div>
</template>

<script>
	
	export default {
		name: 'MovieCard',
		data() {
			return {
				movie: {},
				hours: 0,
				minutes: 0,
				premierYear: 0,
				pattern: /&#?[a-z0-9]+;/ig,
				length: 0
			}
		},
		computed: {
			id() {
				return this.$route.params.id;
			},
			movieDuration() {
				return this.hours + this.minutes;
			}
		},
		created() {
			fetch(`https://kinopoiskapiunofficial.tech/api/v2.1/films/${this.id}`, {
					headers: {
						"Content-type": "application/json",
						"X-API-KEY": "eb848b8b-f82d-40c7-a718-db7c1d9640c3"
					}
			})
				.then(response => response.json())
				.then(response => {
					return response.data;
				})
				.then(data => {
					this.movie = data;
					this.premierYear = data.premiereRu.split('-').reverse().join('-').replace(/-/g, '.');
					this.hours = parseInt(data.filmLength.split(':')[0]) * 60;
					this.minutes = parseInt(data.filmLength.split(':')[1]);
					this.length = data.facts.length;
				})
				.catch(e => console.log(e));
		},

	};

</script>

<style lang="scss" scoped>
	
	.movie-card {
		padding: 20px 15px;
		background: $main-color;
		color: $white;
		text-align: left;
	}

	.card-main {

		display: flex;
		align-items: flex-start;
		justify-content: space-around;

		&__information {
			max-width: 600px;
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}

		&__information p {
			@include adaptiv-font(24, 16);
		}

		&__img-container {
			margin-top: 30px;
			min-width: 15%;
			margin-right: 40px;
		}

		&__img	{
			width: 100%;
			object-fit: cover;
		}

		&__title {
			@include adaptiv-font(48, 24);
			text-align: left;
			margin-bottom: 20px;
		}

		&__about {
			align-self: flex-start;
		}

		&__item {
			margin: 15px 0;
		}
	}

	.movie-facts {

		margin: 60px auto;

		&__title {
			@include adaptiv-font(28, 20);
			text-align: center;
			margin-bottom: 40px;
		}

		&__item {
			@include adaptiv-font(20, 16);
			margin: 15px 0;
		}
	}

	.genre-span {
		display: inline-block;
		margin: 0 5px;
	}

	.item-name {
		display: inline-block;
		margin-right: 10px;
	}

	.button-link {
		@include adaptiv-font(24, 16);
		display: inline-block;
		padding: 20px 25px;
		border-radius: 5px;
		color: yellow;
		background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(57,9,121,0.7290266448376226) 65%, rgba(0,212,255,1) 100%);
		align-self: flex-start;
		margin-top: 30px;
	}

	.home-link {
			position: fixed;
			top: 20px;
			left: 20px;
			z-index: 5;
			color: rgba(pink, 0.7)
	}

	@media (max-width: 850px) {
			.card-main {
				flex-direction: column;
				align-items: center;
				justify-content: center;

					&__img {
						margin: 0 auto;
						max-height: 600px;
						margin-bottom: 30px;
				}
			}
	}

</style>