import { createStore } from 'vuex';
import { makeRequest } from '@/api/server';

export default createStore({
  state: {
    films: [],
    currentPage: 1,
    mainUrl: 'https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=TOP_250_BEST_FILMS&page='
  },
  getters: {
      films: state => state.films.films,
      pagesCount: state => state.films.pagesCount,
      currentPage: state => state.currentPage,
  },
  mutations: {
      setFilms(state, films) {
          state.films = films;
      },
      setCurrentPage(state, number) {
          state.currentPage = number;
      },
      setUrl(state, url) {
          state.mainUrl = url;
          state.currentPage = 1;
      }
  },
  actions: {
      setBaseUrl(store) {
        let baseUrl = 'https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=TOP_250_BEST_FILMS&page=';
        store.commit('setUrl', baseUrl);
      },
      async load(store, id = 1) {
         let films =  await makeRequest(`${store.state.mainUrl}${id}`);
         store.commit('setFilms', films);
      },
      setPage(store, number) {
          store.commit('setCurrentPage', number);
      },
      async changeFilms(store, movie) {
        let newMovie = await makeRequest(`https://kinopoiskapiunofficial.tech/api/v2.1/films/search-by-keyword?keyword=${movie}&page=1`);
        let nextUrl = `https://kinopoiskapiunofficial.tech/api/v2.1/films/search-by-keyword?keyword=${movie}&page=`;
        console.log(newMovie);
        store.commit('setFilms', newMovie);
        store.commit('setUrl', nextUrl);
      },
      async loadTop100(store) {
        let queryParam = 'TOP_100_POPULAR_FILMS';
        let movies = await makeRequest(`https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=${queryParam}&page=1`);
        let nextUrl = `https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=${queryParam}&page=`;
        store.commit('setFilms', movies);
        store.commit('setUrl', nextUrl);
      },
      async loadAwaitMovies(store) {
        let queryParam = 'TOP_AWAIT_FILMS';
        let movies = await makeRequest(`https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=${queryParam}&page=1`);
        let nextUrl = `https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=${queryParam}&page=`;
        store.commit('setFilms', movies);
        store.commit('setUrl', nextUrl);
      },
  },
  
});
